.recordsContainer {
  /* background-color: #e0efda; */
  border: 2rem solid #e0efda;
  margin: 0;
  padding-top: 5rem;
  min-height: 92vh;
}

.recordsContainer img {
  border-radius: 15px;
}

.itemSection {
  /*   display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /*   height: 100vh;  */
  overflow: scroll;
  overflow-x: hidden;
}

#searchBar,
#labelSearch {
  width: 20%;
  margin: 0 1rem;
}

#spanPages {
  visibility: hidden;
}

#spanSearch {
  background-color: #e0efda;
  padding: 0.8rem;
}

h1 {
  padding-bottom: 3rem;
}

.recordsContainer button {
  width: 50%;
}

