/* ///////////////// */

.shopCard {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  background-color: white;
  width: 400px;
  margin: 1rem;
  padding: 1rem;
  font-size: 1rem;
}

.shopCard:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

img {
  border-radius: 5px 5px 0 0;
}

.container {
  padding: 2px 16px;
}

.shopCard h3 {
  padding-top: 1rem;
}

@media (max-width: 768px) {
  #searchBar {
    width: 50%;
  }

  .shopCard {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 5px;
    background-color: white;
    width: 100%;
    margin: 1rem;
    padding: 1rem;
  }
  #spanSearch {
    background-color: #e0efda;
    padding: 0.8rem;
    font-size: 1rem;
  }
}
